
// STYLINGS :: DROPDOWN
// ––––––––––––––––––––––––––––––

.rat-select.theme-#{$theme-name} .select-dropdown {
    top: 100%;
    left: 0;
    color: $color-dark;
    height: auto;
    max-height: 0;
    margin-top: -1px;
    z-index: 15;
    overflow: hidden;
    position: absolute;
    background-color: $color-light;
    border-width: 0 1px 1px 1px;
    border-style: solid;
    border-color: transparent;
    border-radius: 0 0 $border-radius $border-radius;
    transition: max-height 142ms linear, border-color 142ms linear, z-index 0ms linear 142ms;

    // Used for height calculation
    &.cloned {
        min-height: auto;
        max-height: none;
        opacity: 0;
        visibility: hidden;
    }

    .dropdown-inner {
        position: relative;
        overflow-x: hidden;
        overflow-y: auto;
    }
    ol {
        li {
            color: $color-dark;
            padding: 7px 10px 7px 35px;
            position: relative;
            font-size: 95%;
            text-align: left;
            font-weight: normal;
            text-shadow: none;
        }
        li.dropdown-option {
            cursor: pointer;
            color: $color-dark;
            transition: color 142ms linear, background-color 142ms linear;

            .option-description {
                color: lighten($color-dark, 25%);
                width: auto;
                margin: 0;
                padding: 0 2px;
                display: block;
                font-size: 12px;
                text-align: left;
            }
            &:before {
                top: 50%;
                left: 10px;
                width: 15px;
                height: 15px;
                margin: -7.5px 0 0 0;
                padding: 0;
                z-index: 21;
                display: inline-block;
                content: "";
                opacity: 0;
                position: absolute;
                vertical-align: top;
                background-repeat: no-repeat;
                background-position: center center;
                transition: opacity 250ms ease;
            }
            &:hover,
            &.hover {
                color: $color-dark;
                background-color: fadeout($color-primary, 92.5%);
            }
            &.disabled, 
            &.hidden {
                cursor: not-allowed;
                color: fadeout($color-dark, 50%);
                background-color: fadeout($color-dark, 95.175%);
            }
            &.selected {
                color: $color-primary;
            }
        }
        li.optgroup-title {
            padding-left: 10px;
            display: flex;
            justify-content: stretch;
            font-size: 90%;
            font-weight: 600;
            background-color: $color-light;

            button {
                opacity: 0;
            }
            button[data-action="select"] {
                margin-left: auto;
                margin-right: 5px;
            }
            &.optgroup-hidden {
                display: none;
            }
        }
        &:hover li.optgroup-title button {
            opacity: 1;
        }
        &.optgroup-sticky li.optgroup-title {
            top: -1px;
            z-index: 50;
            position: sticky;
        }
    }

    // Title Overflows
    &.overflow-break {
        li {
            overflow: visible;
            white-space: normal;
            text-overflow: none;
        }
    }
    &.overflow-clip {
        li {
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }
    &.overflow-scroll {
        li {
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;

            &[style]:hover span.option-title,
            &[style].hover span.option-title {
                animation: rat_select_text_scroll 2.0s infinite ease-in-out 250ms;
                -webkit-animation: rat_select_text_scroll 2.0s infinite ease-in-out 250ms;
            }
        }
    }
}
.rat-select.theme-#{$theme-name} {
    &.no-animation {
        .select-dropdown,
        &.active .select-dropdown {
            transition: none;
        }
    }
    .select-dropdown.cloned {
        &, .dropdown-inner {
            max-height: none !important;
            transition: none !important;
        }
    }
    &.active .select-dropdown {
        z-index: 25;
        border-color: map-get($border-line-active, light);
        box-shadow: map-get($shadow-line-active, light);
        transition: max-height 142ms linear, border-color 142ms linear, z-index 0ms linear 0ms;
    }
    &.open-top .select-dropdown {
        top: auto;
        bottom: 100%;
        margin: 0 0 -1px 0;
        border-top-width: 1px;
        border-bottom-width: 0;
        border-radius: $border-radius $border-radius 0 0;
    }
    &.active.open-top .select-dropdown {
        box-shadow: list.set-nth(map-get($shadow-line-active, light), 2, -1px);
    }
}


// STYLINGS :: DROPDOWN STYLEs
// ––––––––––––––––––––––––––––––

.rat-select.theme-#{$theme-name} {
    &.hide-selected .select-dropdown li.dropdown-option.selected,
    &.hide-disabled .select-dropdown li.dropdown-option.disabled,
    &.hide-hidden .select-dropdown li.dropdown-option.hidden {
        display: none;
    }

    // State :: Normal
    .select-dropdown li.dropdown-option {
        &:before {
            @include icon("select", $color-dark);
        }
        &:hover:before, 
        &.hover:before {
            opacity: 0.5;
        }
    }
    &.multiple .select-dropdown li.dropdown-option {
        &:before {
            @include icon("m-select", $color-dark);
        }
    }

    // State :: Selected
    .select-dropdown li.dropdown-option.selected {
        &:before {
            opacity: 0.85;
            @include icon("selected", $color-primary);
        }
    }
    &.multiple .select-dropdown li.dropdown-option.selected {
        &:before {
            @include icon("m-selected", $color-primary);
        }
    }

    // State :: Deselect
    &.deselect .select-dropdown li.dropdown-option.selected:not(.disabled):not(.hidden) {
        &:hover, 
        &.hover {
            color: $color-secondary;
            background-color: fadeout($color-secondary, 95.175%);

            &:before {
                opacity: 0.85;
                @include icon("deselect", $color-secondary);
            }
        }
    }
    &.multiple .select-dropdown li.dropdown-option.selected:not(.disabled):not(.hidden) {
        &:hover:before, 
        &.hover:before {
            @include icon("m-deselect", $color-dark);
        }
    }

    // State :: Disabled
    .select-dropdown li.dropdown-option.disabled,
    .select-dropdown li.dropdown-option.hidden {
        &:before {
            opacity: 0.5;
            @include icon("disabled", $color-dark);
        }
        &:hover:before, 
        &.hover:before {
            opacity: 0.5;
        }
    }
    &.multiple .select-dropdown li.dropdown-option.disabled,
    &.multiple .select-dropdown li.dropdown-option.hidden {
        &:before {
            @include icon("m-disabled", $color-dark);
        }
    }
}

// Feather Icons
.rat-select.theme-#{$theme-name}.scheme-light-feather {
    ol li.dropdown-option {
        &:before {
            width: 18px;
            height: 18px;
            margin: -9px 0 0 0;
        }
    }
    .select-dropdown li.dropdown-option {
        &:before {
            @include icon("select", $color-dark, "feather");
        }
    }
    &.multiple .select-dropdown li.dropdown-option {
        &:before {
            @include icon("m-select",  $color-dark, "feather");
        }
    }

    // State :: Selected
    .select-dropdown li.dropdown-option.selected {
        &:before {
            @include icon("selected", $color-dark, "feather");
        }
    }
    &.multiple .select-dropdown li.dropdown-option.selected {
        &:before {
            @include icon("m-selected", $color-dark, "feather");
        }
    }

    // State :: Deselect
    &.deselectable .select-dropdown li.dropdown-option.selected:not(.disabled):not(.hidden) {
        &:hover:before, &.hover:before {
            @include icon("deselect", $color-dark, "feather");
        }
    }
    &.multiple .select-dropdown li.dropdown-option.selected:not(.disabled):not(.hidden) {
        &:hover:before, &.hover:before {
            @include icon("m-deselect", $color-dark, "feather");
        }
    }

    // State :: Disabled
    .select-dropdown li.dropdown-option.disabled,
    .select-dropdown li.dropdown-option.hidden {
        &:before {
            @include icon("disabled", $color-dark, "feather");
        }
    }
    &.multiple .select-dropdown li.dropdown-option.disabled,
    &.multiple .select-dropdown li.dropdown-option.hidden {
        &:before {
            @include icon("m-disabled", $color-dark, "feather");
        }
    }
}